import React, { Component } from 'react';
import { TweenMax } from 'gsap';

import './style.css';

export default class Background extends Component {
  componentDidMount() {
    this.ctx = this.canvasEl.getContext('2d');
    this.resize();

    this.ctx.fillStyle = '#ecf0f1';
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.isActive !== nextProps.isActive && nextProps.isActive) {
      let o = {
        p: 0,
      };

      TweenMax.to(o, 0.8, {
        p: 1,
        ease: TweenMax.Power4.easeIn,
        onUpdate: () => {
          this.ctx.clearRect(0, 0, this.width, this.height);
          this.ctx.beginPath();
          this.ctx.arc(
            this.cx,
            this.cy,
            this.maxRadius * 1.2,
            0,
            2 * Math.PI,
            false
          );
          this.ctx.arc(
            this.cx,
            this.cy,
            this.maxRadius * 1.2 * o.p,
            0,
            2 * Math.PI,
            true
          );
          this.ctx.fill();
          this.ctx.closePath();
        },
      });
    }
  }

  resize() {
    this.width = window.innerWidth;
    this.height = window.innerHeight;

    this.cx = 0.05 * this.width + 12;
    this.cy = 0.05 * this.height + 12;

    this.maxRadius = Math.max(this.width, this.height);
    this.canvasEl.setAttribute('width', this.width);
    this.canvasEl.setAttribute('height', this.height);
  }

  render() {
    return <canvas ref={el => (this.canvasEl = el)} className="background" />;
  }
}



